import { Component, OnInit } from '@angular/core';
import { Transport } from 'src/app/sdk/hr/models/transport';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { GaTransportService, GaUntukService, DirektoriVPegawaiPosisiService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { Pegawai, V_pegawai_posisi, Untuk } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private gaTransportService: GaTransportService,
        private gaUntukService: GaUntukService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private routerActive: ActivatedRoute,
        private pegawaiDetailService: PegawaiDetailService,
        private router: Router,
        private direktoriPegawaiService: DirektoriPegawaiService,

    ) { }

    id_transport: string = ''
    id_pegawai: string = ''
    dataTransport: Transport = {}
    dataPegawai: Pegawai = {}
    dataPosisi: V_pegawai_posisi = {}
    dataUntuk: Untuk = {}

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.routerActive.paramMap.subscribe(
            params => {
                if (params.get('id')) {
                    this.id_transport = params.get('id')
                }
                this.getDetailTiket()

            }
        )
    }


    getDetailTiket() {
        this.spinner.show()
        this.gaTransportService.getGaTransportsId(this.id_transport).subscribe(
            data => {
                this.dataTransport = data.data;
                this.getPegawai(data.data.id_pegawai);
                this.getPosisi(data.data.id_pegawai);
                this.getUntuk(data.data.id_untuk);

                this.spinner.hide()
            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            data => {
                this.dataPosisi = data.data;
            }
        )
    }
    getUntuk(id_untuk: string) {
        this.gaUntukService.getGaUntuksId(id_untuk).subscribe(
            data => {
                this.dataUntuk = data.data;
            }
        )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/employee/pegawai-detail', this.id_pegawai, 'transport'])
    }

}
